async function getById(commentId) {
    return HttpService.get(`contact/${commentId}`)
}

function remove(commentId) {
    return HttpService.delete(`contact/${commentId}`)
}

async function save(contact) {
    if (!contact._id) {
        contact.createdAt = Date.now();
        return HttpService.post('contact', contact)
    }
    return HttpService.put(`contact/${contact._id}`, contact)
}

async function uploadImg(ev) {
    const UPLOAD_PRESET = 'dbyshdqg' // Insert yours
    const CLOUD_NAME = 'neoil' // Insert yours
    const UPLOAD_URL = `https://api.cloudinary.com/v1_1/${CLOUD_NAME}/image/upload`
    const FORM_DATA = new FormData();
    // Building the request body
    FORM_DATA.append('file', ev.target.files[0])
    FORM_DATA.append('upload_preset', UPLOAD_PRESET)
    // Sending a post method request to Cloudniarys' API
    try {
        const res = await axios.post(UPLOAD_URL, FORM_DATA)
        return res.data;
    } catch (err) {
        console.error('ERROR!', err)
    }
}