<template>
  <div class="contact-details-content" v-if="user">
    <div class="contact-card-details">
      <div class="card-img"></div>
      <div class="contact-card-name">Name: {{ user.username }}</div>
      <div class="contact-card-name">Email: {{ user.email }}</div>
      <div class="contact-card-instock"></div>
    </div>

    <ul v-for="review in user.givenReviews" :key="review._id">
      <li>
        Title: {{ review.title }} Rating: {{ review.rating }} Contact id:
        {{ review.aboutContact._id }}
      </li>
    </ul>
  </div>
</template>


<script>
import { commentService } from "../services/contact.service";

export default {
  name: "contact-details",
  data() {
    return {
      contact: null,
      reviewToEdit: {
        rating: null,
        title: null,
      },
    };
  },
  computed: {
    inStockTxt() {
      return this.contact.inStock ? "Available" : "Not available";
    },
    user() {
      return this.$store.getters.user
    }
  },
  methods: {
    removeReview(commentId) {
      this.$store.dispatch({
        type: "removeReview",
        id: commentId,
      });
    },
    addReview() {
      this.reviewToEdit.aboutContactId = this.contact._id
      this.$store.dispatch({
        type: "addReview",
        review: JSON.parse(JSON.stringify(this.reviewToEdit)),
      });
    },
    gotoEdit(commentId) {
      this.$router.push("/edit/" + commentId);
    },
    removeContact(commentId) {
      this.$store.dispatch({
        type: "removeContact",
        id: commentId,
      });
    },
    time(time) {
      var newDate = new Date(time).toLocaleDateString();
      return newDate;
    },
    async getUserId(){
      const userId = await commentService.getById(id)
      this.contact = userId;
    }
  },
  created() {
    const id = this.$route.params.id;
    if (id) {
      this.getUserId()
    }
    const isLogin = this.$store.getters.loggedinUser;
    if (!isLogin) this.$router.push("/");

    // const filterBy = { aboutContactId: id}
    // this.$store.dispatch({type: 'loadReviews', filterBy})
    this.$store.dispatch({type: 'loadUser', id})
  },
};
</script>