<template>
  <div class="contact-app">
    <contact-chat v-if="showChat">
      <div class="chat-header" slot="header">
        <h1>Support Chat</h1>
      </div>
      <div class="chat-content">
        <div class="msg" slot>
          <ul>
            <li v-for="msg in chatMsgs" :key="msg.id">
              {{ msg.name }}: {{ msg.txt }}
            </li>
          </ul>
        </div>
      </div>
      <div class="chat-footer" slot="footer">
        <form @submit.prevent="sendMsg">
          <el-input
            type="text"
            placeholder="Type your Message.."
            v-model="emptyMsg.txt"
          /><el-button @click="sendMsg">Send</el-button>
        </form>
        <h4>Coffe Rights 2020 | MissContact Inc</h4>
      </div>
    </contact-chat>

    <div class="support-btn">
      <el-button
        type="success"
        icon="el-icon-message"
        circle
        @click="showChat = !showChat"
      ></el-button>
    </div>
    <contact-filter @changeView="changeView" />
    <contact-list :comments="commentsToShow" @editContact="setEditContact" />
  </div>
</template>

<script>
import commentList from '../components/contact-list';
import commentFilter from '../components/contact-filter';
import commentChat from '../components/contact-chat';
import genderPicker from '../components/gender-picker';
import { commentService } from '../services/contact.service';

export default {
  name: 'contact-app',
  data() {
    return {
      gender: 'male',
      commentToEdit: commentService.getEmpty(),
      showChat: false,
      chatMsgs: [],
      emptyMsg: {
        txt: null,
        name: null,
      },
    };
  },
  computed: {
    commentsToShow() {
      return this.$store.getters.commentsForDisplay;
    },
  },
  components: {
    commentList,
    genderPicker,
    commentChat,
    commentFilter,
  },
  methods: {
    sendMsg() {
      this.emptyMsg.name = 'You';
      this.chatMsgs.push(JSON.parse(JSON.stringify(this.emptyMsg)));
      this.emptyMsg = { txt: null, name: null };
      setTimeout(() => {
        this.chatMsgs.push({ txt: 'Sure thing honey', name: 'Support' });
      }, 1000);
    },
    setEditContact(contact) {
      this.commentToEdit = contact;
    },
    saveContact() {},
    changeView(view) {
      this.$store.dispatch({
        type: 'changeView',
        view,
      });
    },
  },
  created() {
    const isLogin = this.$store.getters.loggedinUser;
    if (!isLogin) this.$router.push('/');
  },
};
</script>
