

<script>
import { Doughnut } from 'vue-chartjs'
var _ = require('lodash');

import { Bar } from 'vue-chartjs'

export default {
  props: ['commentsToShow'],
  extends: Doughnut,
  data: () => ({
    chartdata: {
      labels: ['Funny', 'Adults'],
      datasets: [
        {
          label: 'Contacts by category',
          backgroundColor: '#f87979',
          data: [40, 20]
        }
      ]
    },
    options: {
      responsive: true,
      maintainAspectRatio: false
    }
  }),
  mounted () {
    this.renderChart(this.chartdata, this.options)
  },
  created() {    
    const funny = _.filter(this.commentsToShow, o => o.type === 'Funny')
    const adult = _.filter(this.commentsToShow, o => o.type === 'Adult')
    this.chartdata.datasets[0].data = [funny.length, adult.length]
  },
}
</script>

<style>
  .small {
    max-width: 600px;
    margin:  150px auto;
  }
</style>