<template>
  <tr class="contact-preview">
    <td class="contact-name">
      {{ contact.name }}
    </td>
    <td class="contact-type">
      {{ contact.type }}
    </td>
    <td class="contact-createdat">
      Date
      <!-- {{ contact.createdAt }} -->
    </td>
    <td class="contact-instock">
      {{ contact.inStock }}
    </td>
    <td class="contact-price">
      {{ contact.price }}
    </td>
    <td class="contact-remove">
      <el-button @click="gotoEdit(contact._id)">Edit</el-button>
      <el-button @click="removeContact(contact._id)">X</el-button>
    </td>
  </tr>
</template>


<script>
export default {
  props: {
    contact: Object,
  },
  data() {
    return {};
  },
  components: {},
  methods: {
    gotoEdit(commentId){
      this.$router.push('/edit/' + commentId);
    },
    removeContact(commentId) {
      this.$store.dispatch({
        type: 'removeContact',
        id: commentId,
      });
    },
  },
  created() {},
};
</script>