<template>
  <div class="dashboard">
    <year-chart class="year-chart" />
    <category-chart class="category-chart" :commentsToShow="commentsToShow" />
  </div>
</template>

<script>
import yearChart from "@/components/year-chart";
import categoryChart from "@/components/category-chart";

export default {
  name: "dashboard",
  data() {
    return {
      comments: null,
    };
  },
  components: {
    yearChart,
    categoryChart,
  },
  computed: {
    commentsToShow() {
      return this.$store.getters.commentsForDisplay;
    },
  },
  created() {
    const isLogin = this.$store.getters.loggedinUser;
    if (!isLogin) this.$router.push("/");
  },
};
</script>

<style lang="css" scoped>
.year-chart {
  width: 400px;
}
.category-chart {
  width: 400px;
}
.dashboard {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>