<template>
  <div class="reivew-details-content" v-if="reviews">
    <div class="contact-card-details">
    </div>
    <ul v-for="review in reviews" :key="review._id">
      <li>
        Title: {{review.title}}
        Rating: {{review.rating}}
        User: <router-link :to="userLink(review.byUser._id)"> {{review.byUser.username}}
        </router-link>

      </li>
    </ul>
<pre>
</pre>
  </div>
</template>


<script>

export default {
  name: "reviews-details",
  data() {
    return {
    };
  },
  computed: {
    reviews() {
      return this.$store.getters.reviews
    },
  },
  methods: {
        userLink(userId){
      return '/user/details/' + userId
    }
  },
  created() {
    const isLogin = this.$store.getters.loggedinUser;
    if (!isLogin) this.$router.push("/");
    this.$store.dispatch({type:'loadReviews', filterBy: {}})
  },
};
</script>