<template>
<div class="gender-picker">
    <el-button @click="input('male')" :style="maleStyle">Male</el-button>
    <el-button @click="input('women')" :style="womenStyle">Women</el-button>
    <el-button @click="input('notdefine')" :style="nodefineStyle">I rather not say</el-button>
</div>
</template>


<script>

export default {
  props: {
    value: String
  },
  data() {
    return {
    };
  },
  methods: {
    input(value) {
      this.$emit('input', value);
    }
  },
  computed: {
    maleStyle(){
      return (this.value === 'male') ? 'color: orange' : ''
    },
    womenStyle(){
      return (this.value === 'women') ? 'color: orange' : ''
    },
    nodefineStyle(){
      return (this.value === 'notdefine') ? 'color: orange' : ''
    }
  },
};
</script>