import Vue from 'vue'
import VueRouter from 'vue-router'
import commentApp from '../views/contact-app.vue'
import commentEdit from '../views/contact-edit.vue'
import commentDetails from '../views/contact-details.vue'
import userDetails from '../views/user-details.vue'
import user from '../views/user.vue'
import dashboard from '../views/dashboard.vue'
import reviews from '../views/reviews.vue'
import login from '../views/login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: login
  },
  {
    path: '/contact',
    name: 'Home',
    component: commentApp
  },
  {
    path: '/user',
    name: 'User',
    component: user
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: dashboard
  },
  {
    path: '/reviews',
    name: 'Reviews',
    component: reviews
  },
  {
    path: '/edit/:id?',
    name: 'Edit',
    component: commentEdit
  },
  {
    path: '/details/:id',
    name: 'Details',
    component: commentDetails
  },
  {
    path: '/user/details/:id',
    name: 'User Details',
    component: userDetails
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
