<template>
  <div class="contact-card-preview">
    <div class="card-img">
      <router-link :to="commentLink">
      <img :src="contact.img" alt="" srcset="" />
      </router-link>
    </div>
    <div class="contact-card-name">Name: {{ contact.name }}</div>
    <div class="contact-card-type">Type: {{ contact.type }}</div>
    <div class="contact-card-createdat">
      {{ time(contact.createdAt) }}
    </div>
    <div class="contact-card-instock">
      {{inStockTxt}}
    </div>
    <div class="contact-card-price">
      ${{ contact.price }}
    </div>
    <div class="contact-card-remove">
      <el-button @click="gotoEdit(contact._id)">Edit</el-button>
      <el-button @click="removeContact(contact._id)">X</el-button>
    </div>
  </div>
</template>


<script>
export default {
  props: {
    contact: Object,
  },
  data() {
    return {};
  },
  computed: {
    inStockTxt(){
      return this.contact.inStock ? 'Available' : 'Not available' 
    },
    commentLink(){
      return `/details/${this.contact._id}`
    }
  },
  methods: {
    gotoEdit(commentId) {
      this.$router.push("/edit/" + commentId);
    },
    removeContact(commentId) {
      this.$store.dispatch({
        type: "removeContact",
        id: commentId,
      });
    },
    time(time) {
      var newDate = new Date(time).toLocaleDateString()
      return newDate
    },
  },
  created() {},
};
</script>