<template>
  <div class="contact-filter">
    <div class="input-container">
      <div class="input-txt">Search</div>
      <div class="input">
        <el-input
          type="text"
          v-model="filterBy.term"
          @input="setFilter"
          placeholder="Type search term.."
        />
      </div>
    </div>
    <div class="input-container">
      <div class="input-txt">In Stock</div>
      <div class="input">
        <el-checkbox v-model="filterBy.inStock" @change="setFilter" />
      </div>
    </div>
    <div class="input-container">
      <div class="input-txt">Type</div>
      <div class="input">
        <el-select
          v-model="filterBy.commentType"
          placeholder="Select"
          @change="setFilter"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="choose-layout">
      <el-popover placement="top" width="160"  v-model="visible">
        <p>Choose view</p>
        <div style="text-align: center; margin: 0">
          <el-button icon="el-icon-s-grid" @click="changeView('grid')" circle></el-button>
          <el-button icon="el-icon-document-copy" @click="changeView('table')" circle></el-button>
        </div>
        <el-button slot="reference">View</el-button>
      </el-popover>
    </div>
  </div>
</template>


<script>
import { commentService } from "../services/contact.service";

export default {
  data() {
    return {
    visible: false,
      filterBy: {
        term: null,
        inStock: null,
        commentType: null,
      },
      options: [
        {
          value: "Funny",
          label: "Funny",
        },
        {
          value: "Adult",
          label: "Adult",
        },
      ],
      value: "",
    };
  },
  components: {},
  methods: {
    changeView(view){
    this.visible = false
    this.$emit('changeView', view)
    },
    setFilter() {
      this.$store.dispatch({
        type: "setFilter",
        filter: JSON.parse(JSON.stringify(this.filterBy)),
      });
    },
  },
};
</script>