<template>
  <div class="contact-edit">
    <div class="title">
      <h2>{{ title }}</h2>
    </div>
    <div v-if="commentToEdit" class="img-gallery">
      <img
        class="contact-edit-img"
        :src="commentToEdit.img"
        alt=""
        srcset=""
      />
    </div>
    <div class="input-name">
      Name:
      <input type="text" v-model="commentToEdit.name" required />
    </div>
    <div class="input-type">
      Type:
      <input type="text" v-model="commentToEdit.type" required />
    </div>
    <div class="input-type">
      Price:
      <input type="number" v-model.number="commentToEdit.price" required />
    </div>
    <div class="input-inStock">
      In Stock:
      <select v-model="commentToEdit.inStock">
        <option value="true">Yes</option>
        <option value="false">No</option>
      </select>
    </div>
    <div class="img-upload">
      <template v-if="!isLoading">
        
      <label for="imgUpload">Upload ☁️</label>
      </template>
      <img v-else class="loading-img" src="https://i.pinimg.com/originals/8e/e2/12/8ee212dac057d412972e0c8cc164deee.gif" alt="">

      <input type="file" name="imgUpload" id="imgUpload" class="img-input" @change="uploadImg" />
    </div>
    <button @click="saveContact">Save</button>
  </div>
</template>


<script>
import { commentService } from "../services/contact.service";

export default {
  name: "contact-edit",
  data() {
    return {
      commentToEdit: commentService.getEmpty(),
      isLoading: false
    }
  },
  computed: {
    title() {
      return this.commentToEdit._id ? "Edit Contact" : "Add New Contact";
    },
  },
  methods: {
    async uploadImg(ev) {
      this.isLoading = true
      const res = await commentService.uploadImg(ev);
      this.commentToEdit.img = res.url;
      this.isLoading = false
    },
    editContact(contact) {
      this.$emit("editContact", JSON.parse(JSON.stringify(contact)));
    },
    removeContact(commentId) {
      this.$store.dispatch({
        type: "removeContact",
        id: commentId,
      });
    },
    saveContact() {
      // if (this.img) this.commentToEdit.img = this.img
      this.$store.dispatch({
        type: "saveContact",
        contact: JSON.parse(JSON.stringify(this.commentToEdit)),
      });
      this.$router.push("/");
    },
  },
  async created() {
    const id = this.$route.params.id;
    if (id) {
      const contact = await commentService.getById(id);
      this.commentToEdit = contact;
    }
    const isLogin = this.$store.getters.loggedinUser;
    if (!isLogin) this.$router.push("/");
  },
};
</script>

<style lang="css">
.img-input {
  position: absolute;
  opacity: 0;
}
.contact-edit-img {
  width: 250px;
}
.loading-img {
  width: 200px;
}
</style>