function query(filterBy) {
    const { aboutContactId } = filterBy
    var queryURL = '?'
    // var queryURL = '?q='
    aboutContactId ? queryURL += `&aboutContactId=${aboutContactId}` : ''
    return HttpService.get(`review/${queryURL}`)
}

async function getById(id) {
    var res = await axios.get(`${baseUrl}/${id}`)
    return res.data
}

function remove(reviewId) {
    return HttpService.delete(`review/${reviewId}`)
}


function add(review) {
    return HttpService.post(`review`, review)
}