<template>
  <div class="contact-list">
    <div class="contact-cards" v-if="showCards">
              <contact-cards
          v-for="contact in comments"
          :contact="contact"
          :key="contact._id"
          @editContact="editContact"
        />
    </div>
    <div class="contact-table" v-if="showTable">
    <table>
      <thead>
        <tr>
          <td @click="setSort('name')">Name</td>
          <td @click="setSort('type')">Type</td>
          <td @click="setSort('craetedAt')">Date</td>
          <td @click="setSort('inStock')">In Stock</td>
          <td @click="setSort('price')">Price</td>
          <td>Actions</td>
        </tr>
      </thead>
      <tbody>
        <contact-table
          v-for="contact in comments"
          :contact="contact"
          :key="contact._id"
          @editContact="editContact"
        />
      </tbody>
    </table>
    </div>

  </div>
</template>


<script>
import commentTable from "../components/contact-table";
import commentCards from "../components/contact-cards";

export default {
  props: {
    comments: Array,
  },
  data() {
    return {
    };
  },
  components: {
    commentTable,
    commentCards
  },
  computed: {
    showTable(){
        return  this.$store.getters.showTable
    },
    showCards(){
        return  this.$store.getters.showCards
    }
  },
  methods: {
    editContact(contact) {
      this.$emit("editContact", contact);
    },
    setSort(sort) {
      this.$store.dispatch({
        type: "setSort",
        sort
      });
    }
  },
};
</script>