import { commentService } from '@/services/contact.service';

export default {
  strict: true,
  state: {
    comments: [],
    filterBy: {
      term: null,
      inStock: false,
      commentType: null,
      sortBy: null
    },
    showTable: false,
    showCards: true
  },
  getters: {
    commentsForDisplay(state) {
      return state.comments
    },
    showTable(state) {
      return state.showTable
    },
    showCards(state) {
      return state.showCards
    }
  },
  mutations: {
    setContacts(state, { comments }) {
      state.comments = comments
    },
    saveContact(state, { savedContact }) {
      state.comments.push(savedContact)
    },
    changeView(state, { view }) {
      if (view === 'grid') {
        state.showTable = false;
        state.showCards = true;
      } else {
        state.showCards = false;
        state.showTable = true;
        console.log(state.showTable);
      }
    },
    updateContact(state, { savedContact }) {
      const idx = state.comments.findIndex(currContact => currContact._id === savedContact._id)
      state.comments.splice(idx, 1, savedContact)
    },
    removeContact(state, { id }) {
      const contact = state.comments.find(contact => contact._id === id)
      const idx = state.comments.findIndex(contact => contact._id === id)
      state.comments.splice(idx, 1)
    },
    setFilter(state, { filter }) {
      state.filterBy = filter
    },
    setSort(state, { sort }) {
      state.filterBy = sort
    }
  },
  actions: {
    async loadContacts({ commit }) {
      try {
        var comments = await commentService.query()
        commit({ type: 'setContacts', comments })
      } catch (err) {
        console.log(`Had issues getting from server`, err)
      }
    },
    changeView({ commit }, { view }) {
      commit({ type: 'changeView', view })
    },
    async saveContact(context, { contact }) {
      var savedContact;
      if (contact._id) {
        savedContact = await commentService.save(contact)
        context.commit({ type: 'updateContact', savedContact })
      } else {
        savedContact = await commentService.save(contact)
        context.commit({ type: 'saveContact', savedContact })
      }
    },
    async removeContact(context, { id }) {
      await commentService.remove(id)
      context.commit({ type: 'removeContact', id })
    },
    setFilter({ commit, state }, { filter }) {
      commit({ type: 'setFilter', filter })
      commentService.query(state.filterBy)
        .then(comments => {
          commit({ type: 'setContacts', comments })
        })
    },
    setSort({ commit, state }, { sort }) {
      commit({ type: 'setSort', sort })
      commentService.query(state.filterBy)
        .then(comments => {
          commit({ type: 'setContacts', comments })
        })
    }
  }
}
