

<script>
import { Line } from 'vue-chartjs'

export default {
  extends: Line,
  mounted () {
    // Overwriting base render method with actual data.
    this.renderChart({
      labels: [2016, 2017, 2019, 2020],
      datasets: [
        {
          label: 'Contacts created over the years',
          backgroundColor: '#f87979',
          data: [40, 95, 105, 330]
        }
      ]
    })
  }
}
</script>

<style>
  .small {
    max-width: 600px;
    margin:  150px auto;
  }
</style>