<template>
  <div class="contact-details-content" v-if="contact">
    <div class="contact-card-details">
      <div class="card-img">
        <img :src="contact.img" alt="" srcset="" />
      </div>
      <div class="contact-card-name">Name: {{ contact.name }}</div>
      <div class="contact-card-type">Type: {{ contact.type }}</div>
      <div class="contact-card-createdat">
        {{ time(contact.createdAt) }}
      </div>
      <div class="contact-card-instock">
        {{ inStockTxt }}
      </div>
      <div class="contact-card-price">${{ contact.price }}</div>
    </div>
    <div class="reviews"></div>
    <div class="add-review">
      <div class="add-review-txt">
        <h2>Add Review:</h2>
      </div>
      <div class="input-name">
        Text:
        <textarea v-model="reviewToEdit.title" required />
      </div>
      <div class="input-type">
        Rating:
        <select v-model="reviewToEdit.rating">
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
        </select>
      </div>
      <button @click="addReview">Add Review</button>
    </div>
    <ul v-for="review in contact.givenReviews" :key="review._id">
      <li>
        Title: {{ review.title }} Rating: {{ review.rating }} User:
        <router-link :to="userLink(review.byUser._id)">
          {{ review.byUser.username }}
        </router-link>
      </li>
    </ul>
    <pre></pre>
  </div>
</template>


<script>
import { commentService } from '../services/contact.service';

export default {
  name: 'contact-details',
  data() {
    return {
      contact: null,
      reviewToEdit: {
        rating: null,
        title: null,
      },
    };
  },
  computed: {
    inStockTxt() {
      return this.contact.inStock ? 'Available' : 'Not available';
    },
    reviews() {
      return this.$store.getters.reviews;
    },
  },
  methods: {
    removeReview(commentId) {
      this.$store.dispatch({
        type: 'removeReview',
        id: commentId,
      });
    },
    userLink(userId) {
      return '/user/details/' + userId;
    },
    addReview() {
      this.reviewToEdit.aboutContactId = this.contact._id;
      this.$store.dispatch({
        type: 'addReview',
        review: JSON.parse(JSON.stringify(this.reviewToEdit)),
      });
    },
    gotoEdit(commentId) {
      this.$router.push('/edit/' + commentId);
    },
    removeContact(commentId) {
      this.$store.dispatch({
        type: 'removeContact',
        id: commentId,
      });
    },
    time(time) {
      var newDate = new Date(time).toLocaleDateString();
      return newDate;
    },
  },
  created() {
    const id = this.$route.params.id;
    if (id) {
      commentService.getById(id).then((contact) => {
        this.contact = contact;
      });
    }
    const isLogin = this.$store.getters.loggedinUser;
    if (!isLogin) this.$router.push('/');
    const filterBy = { aboutContactId: id };
    this.$store.dispatch({ type: 'loadReviews', filterBy });
  },
};
</script>