<template>
  <div class="login">
    <div class="login-form">
      <form @submit.prevent="doLogin">
        <div class="input-name">
          <label>
            Email:
            <input type="text" v-model="loginCred.email" />
          </label>
        </div>
        <div class="input-type">
          <label>
            Password:
            <input type="password" v-model="loginCred.password" />
          </label>
        </div>
        <button>Login</button>
      </form>
    </div>
    <div class="signup-form">
      <form @submit.prevent="doSignup">
        <div class="input-name">
          <label>
            Username:
            <input type="text" v-model="signupCred.username" />
          </label>
        </div>
        <div class="input-name">
          <label>
            Email:
            <input type="text" v-model="signupCred.email" />
          </label>
        </div>
        <div class="input-type">
          <label>
            Password:
            <input type="password" v-model="signupCred.password" />
          </label>
        </div>
        <button>Login</button>
      </form>
    </div>
    {{ msg }}
  </div>
</template>


<script>
import { userService } from "../services/user.service";

export default {
  name: "login",
  data() {
    return {
      loginCred: {
        email: "neho",
        password: "neho",
        // loggedInUser: this.$store.getters.loggedinUser
      },
      signupCred: {},
      msg: "",
    };
  },
  components: {},
  methods: {
    async doLogin() {
      const cred = this.loginCred;
      if (!this.loginCred.email || !this.loginCred.password)
        return (this.msg = "Please enter user/pass");
      await this.$store.dispatch({ type: "login", cred });
      this.$router.push("/contact");
    },
    async doSignup() {
      const cred = this.signupCred;
      if (!cred.email || !cred.password)
        return (this.msg = "Please enter user/pass");
      this.$store.dispatch({ type: "signup", cred });
      this.$router.push("/contact");
      this.signupCred = {};
    }
  },
  created() {
    const isLogin = this.$store.getters.loggedinUser
    if (isLogin) this.$router.push("/contact")
    }
};
</script>